import React from "react";
import { MDBContainer, MDBRow, MDBCol } from "mdbreact";
import "../styles.css";
import submarine2 from "../assets/submarine2.svg";
import waves from "../assets/waves.svg";
import fredag from "../assets/fredag.svg";
import lordag from "../assets/lordag.svg";
import program from "../assets/program.svg";
import walking from "../assets/walking.svg";
import { MDBAnimation, MDBBtn } from "mdbreact";
import Zoom from "react-reveal/Zoom";
import { Controller, Scene } from "react-scrollmagic";
import { Tween, Timeline } from "react-gsap";
import ReactVivus from "react-vivus";

class Submarine extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false
    };
  }

  componentDidMount() {
    setTimeout(() => this.setState({ show: true }), 3700);
  }
  render() {
    return (
      <MDBContainer fluid className="align-middle">
        <MDBRow center className="text-center mt-5">
          <MDBCol md="12">
            <MDBAnimation
              className="submarineAnimation"
              type="shake"
              infinite
              duration="5s"
            >
              <Controller>
                <Scene triggerHook="onEnter" duration={3300}>
                  {progress => (
                    <div>
                      <Timeline totalProgress={progress} paused>
                        <Tween from={{ x: "50%" }} to={{ x: "-160%" }}>
                          <div className="animation">
                            <img
                              src={submarine2}
                              className="submarineIMG"
                              alt=""
                            />
                          </div>
                        </Tween>
                      </Timeline>
                    </div>
                  )}
                </Scene>
              </Controller>
            </MDBAnimation>
            <img src={waves} className="img-fluid waves" alt="" />
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    );
  }
}

export default Submarine;
