import React, { Component } from "react";
import "../styles.css";
import {
  MDBNavbar,
  MDBNavbarBrand,
  MDBNavbarNav,
  MDBNavItem,
  MDBNavLink,
  MDBNavbarToggler,
  MDBCollapse,
  MDBDropdown,
  MDBDropdownToggle,
  MDBDropdownMenu,
  MDBDropdownItem,
  MDBIcon,
  MDBSmoothScroll
} from "mdbreact";
import AnchorLink from "react-anchor-link-smooth-scroll";
import { HashRouter as Router } from "react-router-dom";

class NavbarPage extends Component {
  state = {
    isOpen: false
  };

  toggleCollapse = () => {
    this.setState({ isOpen: !this.state.isOpen });
  };

  render() {
    const bgPink = { fontWeight: 400, color: "black" };
    return (
      <div>
        <Router>
          <header>
            <MDBNavbar className="black-text" fixed="top" dark expand="xl">
              <MDBNavbarBrand>
                <strong className="white-text"></strong>
              </MDBNavbarBrand>
              <MDBNavbarToggler onClick={this.toggleCollapse} />
              <MDBCollapse
                id="navbarCollapse3"
                isOpen={this.state.isOpen}
                navbar
              >
                <MDBNavbarNav left>
                  <MDBNavItem>
                    <MDBNavLink to="#program" className="black-text">
                      {" "}
                      <AnchorLink offset="300" style={bgPink} href="#program">
                        PROGRAM
                      </AnchorLink>
                    </MDBNavLink>
                  </MDBNavItem>
                  <MDBNavItem>
                    <MDBNavLink to="#" style={bgPink}>
                      {" "}
                      <AnchorLink
                        offset="-200"
                        className="black-text"
                        href="#musik"
                      >
                        MUSIK
                      </AnchorLink>
                    </MDBNavLink>
                  </MDBNavItem>
                  <MDBNavItem>
                    <MDBNavLink to="#" className="black-text">
                      {" "}
                      <AnchorLink style={bgPink} href="#kontakt">
                        KONTAKT
                      </AnchorLink>
                    </MDBNavLink>
                  </MDBNavItem>
                  <MDBNavItem>
                    <MDBDropdown>
                      <MDBDropdownToggle style={bgPink} nav caret>
                        SHOWS
                      </MDBDropdownToggle>
                      <MDBDropdownMenu
                        style={bgPink}
                        className="black-text dropdown-default"
                      >
                        <MDBDropdownItem style={bgPink}>
                          <a href="/aarhus">Aarhus</a>
                        </MDBDropdownItem>
                        <MDBDropdownItem style={bgPink}>
                          <a href="/viborg">Viborg</a>
                        </MDBDropdownItem>
                      </MDBDropdownMenu>
                    </MDBDropdown>
                  </MDBNavItem>
                </MDBNavbarNav>
                <MDBNavbarNav right>
                  <MDBNavItem>
                    <a
                      className="custom-style"
                      href="https://www.facebook.com/events/298155257801558/"
                    >
                      <MDBIcon
                        className="black-text"
                        fab
                        icon="facebook-f"
                        size="lg"
                      />
                    </a>
                  </MDBNavItem>
                  <MDBNavItem></MDBNavItem>
                </MDBNavbarNav>
              </MDBCollapse>
            </MDBNavbar>
          </header>
        </Router>
      </div>
    );
  }
}

export default NavbarPage;
