import React from "react";
import beatlesLogo from "../assets/logointro.svg";
import forever from "../assets/jump.svg";
import letitbe from "../assets/letitbe.svg";
import "../styles.css";
import {
  MDBContainer,
  MDBRow,
  MDBCol,
  MDBAnimation,
  MDBBtn,
  MDBMask,
  MDBView
} from "mdbreact";
import ReactVivus from "react-vivus";
import Zoom from "react-reveal/Zoom";
import { MDBSticky, MDBStickyContent } from "mdbreact";

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false
    };
  }

  componentDidMount() {
    setTimeout(() => this.setState({ show: true }), 3700);
  }

  render() {
    return (
      <MDBContainer className="mt-5 pt-5">
        <MDBRow center>
          <MDBCol md="7">
            {" "}
            <img src={beatlesLogo} alt="logo" class="img-fluid" />
          </MDBCol>
        </MDBRow>
        <MDBRow center className="text-center align-middle mt-5">
          <MDBCol md="5">
            {" "}
            <ReactVivus
              id="foo2"
              className={this.state.show ? "svg-show" : "svg"}
              option={{
                file: forever,
                duration: 200,
                animTimingFunction: "oneByOne",
                type: "oneByOne",
                onReady: console.log
              }}
              callback={console.log}
            />
          </MDBCol>
        </MDBRow>
        <MDBRow center className="text-center mt-5">
          <MDBCol md="12">
            <img src={letitbe} alt="logo" class="img-fluid mb-2" />
            <p className="h4">50-ÅRS HYLDESTFEST</p>
            <MDBBtn
              size="lg"
              href="/aarhus"
              className="ticketButton"
              color="deep-orange"
            >
              Aarhus
            </MDBBtn>
            <MDBBtn
              size="lg"
              href="/viborg"
              className="ticketButton"
              color="deep-orange"
            >
              Viborg
            </MDBBtn>
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    );
  }
}

export default Home;
