import React from "react";
import ReactDOM from "react-dom";
import "@fortawesome/fontawesome-free/css/all.min.css";
import "bootstrap-css-only/css/bootstrap.min.css";
import "mdbreact/dist/css/mdb.css";
import "./fonts/Lennon.ttf";
import "./fonts/Swiss.ttf";
import "./fonts/Swiss-721-Bold-BT_43217.ttf";
import "./index.css";
import "./styles.css";
import Aarhus from "./aarhus.js";
import Viborg from "./viborg.js";
import App from "./App";
import ReactGA from "react-ga";
import { BrowserRouter, Router, Route, Switch } from "react-router-dom";
import ReactPixel from "react-facebook-pixel";
import registerServiceWorker from "./registerServiceWorker";

const routing = (
  <BrowserRouter>
    <Switch>
      <div>
        <Route exact path="/" component={App} />
        <Route path="/aarhus" component={Aarhus} />
        <Route path="/viborg" component={Viborg} />
      </div>
    </Switch>
  </BrowserRouter>
);
ReactDOM.render(routing, document.getElementById("root"));

registerServiceWorker();
