import React from "react";
import beatlesLogo2 from "../assets/BealtesLogo2.png";
import forever from "../assets/jump.svg";
import "../styles.css";
import { MDBContainer, MDBRow, MDBCol, MDBAnimation, MDBBtn } from "mdbreact";
import ReactVivus from "react-vivus";
import Zoom from "react-reveal/Zoom";
import { MDBSticky, MDBStickyContent } from "mdbreact";

class Home extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      show: false
    };
  }

  componentDidMount() {
    setTimeout(() => this.setState({ show: true }), 3700);
  }

  render() {
    return (
      <MDBContainer className="align-middle ">
        <MDBRow center className="text-center">
          <MDBCol md="7">
            {" "}
            <img src={beatlesLogo2} alt="logo" class="img-fluid" />
          </MDBCol>
        </MDBRow>
        <MDBRow center className="text-center">
          <MDBCol md="5">
            {" "}
            <ReactVivus
              id="foo2"
              className={this.state.show ? "svg-show" : "svg"}
              option={{
                file: forever,
                duration: 200,
                animTimingFunction: "oneByOne",
                type: "oneByOne",
                onReady: console.log
              }}
              callback={console.log}
            />
          </MDBCol>
        </MDBRow>
        <MDBRow center className="text-center mt-5">
          <MDBCol md="12">
            {" "}
            <p className="h4"> LET IT BE 50 ÅRS JUBILÆUMSFEST</p>
          </MDBCol>
        </MDBRow>
        <MDBRow center className="text-center mt-5">
          <MDBCol md="12">
            {" "}
            <MDBBtn
              target="_blank"
              href="https://www.ticketmaster.dk/event/475141?fbclid=IwAR0v8K_gnkgAqybecj2u0-CFtSS-B48ZxRGzUE79IzmMhoM8x82QJw_tqeM"
              className="ticketButton"
              color="deep-orange"
            >
              {" "}
              Køb officielle billetter
            </MDBBtn>
          </MDBCol>
        </MDBRow>
      </MDBContainer>
    );
  }
}

export default Home;
