import React from "react";
import {
  MDBRow,
  MDBCol,
  MDBCard,
  MDBCardBody,
  MDBIcon,
  MDBBtn,
  MDBInput
} from "mdbreact";
import Zoom from "react-reveal/Zoom";

const ContactPage = () => {
  return (
    <section id="kontakt" className="my-5">
      <h2 className="h1-responsive font-weight-bold text-center">Kontakt os</h2>
      <p className="text-center w-responsive mx-auto pb-5">
        Find mail, telefonnummer og Facebook-link, så du kan komme i kontakt med
        os kundeservice så hurtigt som muligt.
      </p>
      <MDBRow>
        <MDBCol lg="12">
          <div
            id="map-container"
            className="rounded z-depth-1-half map-container"
            style={{ height: "400px" }}
          >
            <iframe
              src="https://www.google.com/maps/embed?pb=!1m18!1m12!1m3!1d2222.2271572183836!2d10.199654216237835!3d56.15317698066234!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x464c3f925e02c0a7%3A0xbf84a1cac0a6b0cf!2sVester+All%C3%A9+1%2C+8000+Aarhus!5e0!3m2!1sen!2sdk!4v1563744480078!5m2!1sen!2sdk"
              title="This is a unique title"
              width="100%"
              height="100%"
              frameBorder="0"
              style={{ border: 0 }}
            />
          </div>
          <br />
          <MDBRow className="text-center">
            <MDBCol md="4">
              <Zoom duration={1000}>
                <MDBBtn tag="a" floating color="blue" className="ticketButton">
                  <MDBIcon icon="map-marker-alt" size="2x" />
                </MDBBtn>
                <p>Vester Allé 1, 8000 Aarhus</p>
              </Zoom>
            </MDBCol>
            <MDBCol md="4">
              <Zoom duration={1000}>
                <MDBBtn tag="a" floating color="blue" className="ticketButton">
                  <MDBIcon icon="phone" size="2x" />
                </MDBBtn>
                <p>25 21 92 44</p>
              </Zoom>
            </MDBCol>
            <MDBCol md="4">
              <Zoom duration={1000}>
                <MDBBtn tag="a" floating color="blue" className="ticketButton">
                  <MDBIcon icon="envelope" size="2x" />
                </MDBBtn>
                <p>kontakt@foreverthebeatles.dk</p>
              </Zoom>
            </MDBCol>
          </MDBRow>
        </MDBCol>
      </MDBRow>
    </section>
  );
};

export default ContactPage;
