import React from "react";
import {
  MDBRow,
  MDBCol,
  MDBCard,
  MDBCardBody,
  MDBIcon,
  MDBBtn,
  MDBInput
} from "mdbreact";
import Zoom from "react-reveal/Zoom";

const ContactPageViborg = () => {
  return (
    <section id="kontakt" className="my-5">
      <h2 className="h1-responsive font-weight-bold text-center">Kontakt os</h2>
      <p className="text-center w-responsive mx-auto pb-5">
        Find mail, telefonnummer og Facebook-link, så du kan komme i kontakt med
        os kundeservice så hurtigt som muligt.
      </p>
      <MDBRow>
        <MDBCol lg="12">
          <div
            id="map-container"
            className="rounded z-depth-1-half map-container"
            style={{ height: "400px" }}
          >
            <iframe
              src="https://www.google.com/maps/embed?pb=!1m14!1m8!1m3!1d8818.997532557665!2d9.403821!3d56.45485!3m2!1i1024!2i768!4f13.1!3m3!1m2!1s0x0%3A0x47cfc03cb9c34e5e!2sTinghallen%20A%2FS!5e0!3m2!1sen!2sdk!4v1571152937408!5m2!1sen!2sdk"
              title="This is a unique title"
              width="100%"
              height="100%"
              frameBorder="0"
              style={{ border: 0 }}
            />
          </div>
          <br />
          <MDBRow className="text-center">
            <MDBCol md="4">
              <Zoom duration={1000}>
                <MDBBtn tag="a" floating color="blue" className="ticketButton">
                  <MDBIcon icon="map-marker-alt" size="2x" />
                </MDBBtn>
                <p>Tingvej 2, 8800 Viborg</p>
              </Zoom>
            </MDBCol>
            <MDBCol md="4">
              <Zoom duration={1000}>
                <MDBBtn tag="a" floating color="blue" className="ticketButton">
                  <MDBIcon icon="phone" size="2x" />
                </MDBBtn>
                <p>25 21 92 44</p>
              </Zoom>
            </MDBCol>
            <MDBCol md="4">
              <Zoom duration={1000}>
                <MDBBtn tag="a" floating color="blue" className="ticketButton">
                  <MDBIcon icon="envelope" size="2x" />
                </MDBBtn>
                <p>kontakt@foreverthebeatles.dk</p>
              </Zoom>
            </MDBCol>
          </MDBRow>
        </MDBCol>
      </MDBRow>
    </section>
  );
};

export default ContactPageViborg;
