import React, { Component } from "react";
import { MDBBtn, MDBCol, MDBContainer, MDBRow } from "mdbreact";
import "./index.css";
import "./styles.css";
import logo from "./logo.png";
import Home from "./components/home.js";
import Tickets from "./components/tickets.js";
import Submarine from "./components/submarine.js";
import Info from "./components/info.js";
import ContactPage from "./components/contact.js";
import NavbarPage from "./components/navbar.js";
import AnchorLink from "react-anchor-link-smooth-scroll";

import Fade from "react-reveal/Zoom";

class Aarhus extends Component {
  render() {
    return (
      <div>
        <NavbarPage />
        <MDBContainer className="pt-5">
          <MDBRow center className="text-center mb-1">
            <Home />
          </MDBRow>
          <MDBRow className="text-center mb-5 mt-1">
            <Tickets />
          </MDBRow>
          <MDBRow center>
            <Info />
          </MDBRow>
          <MDBRow center className="text-center mb-3 mt-1">
            <Submarine />
            <ContactPage />
          </MDBRow>
        </MDBContainer>
      </div>
    );
  }
}

export default Aarhus;
