import React from "react";
import { MDBContainer, MDBRow, MDBCol } from "mdbreact";
import "../styles.css";
import submarine from "../assets/submarine.svg";
import fredag from "../assets/fredag.svg";
import lordag from "../assets/lordag.svg";
import program from "../assets/program.svg";
import walking from "../assets/walking.svg";
import bandtext from "../assets/bandtext.svg";
import bandpicture from "../assets/bandpicture.jpg";
import newGuy from "../assets/newGuy.jpg";
import jm from "../assets/jm.jpg";

import Fade from "react-reveal/Zoom";

import { MDBAnimation, MDBBtn } from "mdbreact";

class InfoViborg extends React.Component {
  render() {
    return (
      <MDBContainer fluid className="align-middle">
        <section className="text-center " id="musik">
          <h2 className="lineup h1-responsive font-weight-bold my-5">
            Aftenens værter
          </h2>

          <MDBRow center className="mt-5">
            <MDBCol md="10">
              <img src={bandpicture} className="img-fluid" alt="" />

              <p className="text-left ">
                {" "}
                <b className="lineup">LINE UP: </b> Niels Ratzer (trommer), Ole
                Skipper (bas), Josef Baumgartner (stand-in for Søren Skov)
                (keyboard), Troels Skovgaard (guitar) samt af skuespilleren,
                Søren Pilmark (lead vokal, piano og guitar).
              </p>

              <p className="text-left">
                {" "}
                <b className="lineup">FRANCIS McRIDDLEGOUPH</b> - er et band,
                der udelukkende fortolker sange af{" "}
                <b className="lineup">THE BEATLES</b>. Bandet med det finurlige
                navn, opstod i forbindelse med samarbejdet på et One-man show
                med <b className="lineup">Søren Pilmark</b> i 2003. Musikerne og
                Pilmark fandt hurtigt sammen i deres fælles kærlighed og glæde
                ved <b className="lineup">THE BEATLES</b>’ fantastiske musik og
                har lige siden, hvilket vil sige i 15 år, loyalt og trofast mod
                forlægget og forbillederne, spillet og optrådt med Beatles musik
                over det ganske land. Med en smittende spilleglæde fra fire af
                landets suverænt bedste musikere og med Søren Pilmark i front,
                er der lagt op til en Beatles aften af de store.
              </p>
            </MDBCol>

            <MDBCol md="5">
              <img src={jm} className="img-fluid newguy" alt="" />
              <p className="text-left mt-2">
                Festen styres sikkert i hus under kyndig musikalsk vejledning af
                den altid suveræne <b className="lineup">DJ Jørgen de Mylius</b>
                . Husk derfor danseskoene og dem du vil ta' en svingom med.
                Garanti for dansable evergreens og en god aften!
              </p>
            </MDBCol>

            <MDBCol md="5">
              <img src={newGuy} className="img-fluid newguy" alt="" />

              <p className="text-left mt-2">
                Aftenens konferencier, musikjournalist og Beatles-forfatter{" "}
                <b className="lineup">Per Wium</b>, tager os med en tur ned ad
                memory lane, når han fortæller små anekdoter om The Beatles,
                ånden og tiden. Undervejs inviteres flere artister på scenen,
                der fortolker Beatles klassikere med manér.
              </p>
            </MDBCol>
          </MDBRow>
          <MDBRow center className="text-center mt-5">
            <MDBCol md="12">
              <MDBBtn
                target="_blank"
                href="https://www.ticketmaster.dk/event/475141?fbclid=IwAR0v8K_gnkgAqybecj2u0-CFtSS-B48ZxRGzUE79IzmMhoM8x82QJw_tqeM"
                className="ticketButton"
                color="deep-orange"
              >
                {" "}
                Køb officielle billetter
              </MDBBtn>
            </MDBCol>
          </MDBRow>
        </section>
      </MDBContainer>
    );
  }
}

export default InfoViborg;
